import {AVisualComponent} from "../component/abstract/a-visual-component";
import {IRenderable} from "../core/interface/i-renderable";
import {EVerticalAlign} from "../component/enum/e-vertical-align";
import jquery = require("jquery");

/**
 * <img width="100%" src="../../doc_assets/separator_example_1.png">
 * Linha horizontal que representa uma quebra de linha podendo conter cor e tamanho
 * @memberof module:mvcomponents/separator
 * 
 * @example
 *  import {Separator} from "mvcomponents/separator";
 *  import {Form} from "mvcomponents/container";
 *  import {EVerticalAlign} from "mvcomponents/component";
 *
 *  let form:Form = new Form();
 *  form.setSize(12);
 *
 *  let separador:Separator = new Separator();
 *  separador.setColor("blue");
 *  separador.setVerticalAlign(EVerticalAlign.TOP);
 *  form.append(separador);
 */
export class Separator extends AVisualComponent implements IRenderable{
	private _defaultColor:string;
	constructor(){
		super("div",`<div class="separator-line"></div>`);
		this._defaultColor = "#dddddd";
		jquery(this.element)
			.addClass("Separator col-xs-12 col-sm-12 col-md-12 col-lg-12 column")
			.find(".separator-line:first")
			.css({
				"border":"0px solid transparent"
				,"border-style":"solid"
				,"border-bottom-width":"2px"
				,"margin-bottom":"10px"
				,"border-bottom-color":this._defaultColor
			});
	}
	
	/**
	 * @param color {string}
	 * @return {Separator}
	 */
	public setColor(color:string):Separator{
		jquery(this.element)
			.find(".separator-line:first")
			.css({"border-bottom-color":color});
		return this;
	}

	/**
	 * @param height {number}
	 * @return {Separator}
	 */    
	public setHeight(height:number):Separator{
		jquery(this.element)
			.find(".separator-line:first")
			.css({"border-bottom-width":`${height}px`});
		return this;
	}

	/**
	 * @param align {EVerticalAlign}
	 * @return {Separator}
	 */    
	public setVerticalAlign(align:EVerticalAlign):Separator{
		if(align === EVerticalAlign.TOP){
			jquery(this.element).css({
				"margin-top":"0px"
				,"margin-bottom":"20px"
			});
		}else if(align === EVerticalAlign.MIDDLE){
			jquery(this.element).css({
				"margin-top":"10px"
				,"margin-bottom":"0px"
			});
		}else if(align === EVerticalAlign.BOTTOM){
			jquery(this.element).css({
				"margin-top":"20px"
				,"margin-bottom":"0px"
			});
		}
		return this;
	}

	/**
	 * @param transparent {boolean}
	 * @return {Separator}
	 */    
	public setTransparent(transparent:boolean):Separator{
		return this.setColor(transparent?"transparent":"#dddddd");
	}
}
